.btn {
  box-sizing: border-box;
  border-radius: 5px;
  color: #232833;
  font-size: 16px;
  line-height: 22px;
  min-height: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 700;
  transition: 0.4s;
}
.btn i {
  padding-left: 11px;
  column-rule: inherit;
  font-size: 14px;
  transition: 0.4s;
}
.btn:hover i {
  transform: translateX(15%);
}
.btn-primary {
  border: 1px solid #FE8083;
  background-color: #FE8083;
  color: #fff;
}
.btn-secondary {
  border: 1px solid #FE8083;
  background-color: #FE8083;
}
.btn-accent {
  border: 1px solid #FFEEEE;
  background-color: #FFEEEE;
  color: #FE8083;
}
.btn-accent:hover {
  background-color: #FE8083;
  color: #fff;
}
.btn-outline {
  border: 1px solid #D7E0E7;
  background-color: transparent;
  color: #092C4C;
  transition: 0.4s;
}
.btn-outline:hover {
  background-color: #FE8083;
  color: #fff;
}
.btn-outline i {
  margin-right: 10px;
}

