/* feature-course-card */
.feature-course-card {
  position: relative;
  z-index: 1;
  background-color: #fff;
  border-radius: 5px;
}
.feature-course-card__image {
  width: 100%;
  height: 270px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  overflow: hidden;
}
.feature-course-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.feature-course-card__content {
  padding: 24px 24px;
  position: relative;
  z-index: 1;
}
.feature-course-card__content__fee {
  background-color: #FFC258;
  position: absolute;
  top: -20px;
  left: 24px;
  color: #092C4C;
  border-radius: 4px;
}
.feature-course-card__content__fee span {
  font-weight: 600;
  margin-left: 7px;
}
.feature-course-card__content__title {
  font-size: 24px;
  font-weight: 600;
  line-height: 34px;
  color: #092C4C;
}
.feature-course-card__content .feature-course__details {
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.feature-course-card__content .feature-course__details::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 30px;
  background-color: #D7E0E7;
}
.feature-course-card__content .feature-course__details__classes, .feature-course-card__content .feature-course__details__duration {
  display: flex;
}
.feature-course-card__content .feature-course__details__icon {
  width: 34px;
  height: 34px;
  margin-top: 0px;
}
.feature-course-card__content .feature-course__details__icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.feature-course-card__content .feature-course__details__text {
  font-size: 24px;
  font-weight: 700;
  color: #092C4C;
  margin-bottom: 0;
  line-height: 1.1;
}
.feature-course-card__content .feature-course__details__text span {
  display: block;
  font-weight: 400;
  color: #7F92A3;
  font-size: 16px;
}